import React, { Component } from 'react';
import { Modal, ActivityIndicator, List, Button } from 'antd-mobile';
const { alert, operation } = Modal;
import Template from '../Template';
import TransformManager, { TransformManagerItem } from '../TransformManager';
import DetailFactory from './DetailFactory';
import UpdatePage from './UpdatePage';
import MapBox from '../MapBox';
import { formatDate, bindTouchDirection } from '../../utils';
import FunctionalButton from './FunctionalButton';
import SearchBar from './SearchBar';
import Swiper from '../Swiper';
import './css/Container-core.css';
export default class ContainerCore extends Component {
    constructor() {
        super(...arguments);
        this.state = {
            currentOrder: 0,
            currentGroup: 'list-page',
            // for mapPicker
            lng: '-1',
            lat: '-1',
            primaryValue: '',
            currentState: 0,
            updatePageStatus: 'add',
            mapBoxTargetKey: '',
            showSearchBar: false,
            templateHeight: 0,
        };
        this.history = {
            group: 'list-page',
            order: -1,
        };
        this.componentDidMount = () => {
            bindTouchDirection(this.content, direction => {
                switch (direction) {
                    case 'toRight':
                        this.setState({ showSearchBar: true });
                        break;
                    default:
                        break;
                }
            });
        };
        this.backToList = () => {
            if (this.props.onSearch) {
                this.props.onSearch();
            }
            this.setState({ currentGroup: 'list-page', currentOrder: 0 });
        };
        this.handleDelete = (primaryValue) => {
            alert('长痛不如短痛，删tm的', '真删了啊？', [
                { text: '容朕三思' },
                {
                    text: '真的',
                    onPress: () => {
                        if (this.props.onDelete) {
                            this.props.onDelete(primaryValue);
                        }
                    },
                },
            ]);
        };
        this.handleTemplateClick = ({ templateOrder, }) => {
            this.setState({ currentOrder: templateOrder, currentGroup: 'detail-page' });
        };
        // show Modal for operation
        this.handleTemplatePress = (dataItem) => {
            const { primaryKey, power } = this.props;
            const { add, delete: del, update, select } = power;
            const primaryValue = dataItem[primaryKey || ''];
            let param = [];
            if (!select) {
                return;
            }
            if (add) {
                param.push({
                    text: '新增',
                    onPress: () => this.setState({
                        currentGroup: 'update-page',
                        currentOrder: 0,
                        primaryValue,
                        updatePageStatus: 'add',
                    }),
                });
            }
            if (update) {
                param.push({
                    text: '修改',
                    onPress: () => {
                        this.setState({
                            currentGroup: 'update-page',
                            currentOrder: 0,
                            primaryValue,
                            updatePageStatus: 'update',
                        });
                    },
                });
            }
            if (del) {
                param.push({
                    text: '删除',
                    onPress: () => this.handleDelete(primaryValue),
                });
            }
            param.length !== 0 && operation(param);
        };
        this.handleChildDataFormat = (value, childProps, bindKey) => {
            for (let item of this.props.config) {
                const { key, unit, decimalCount, dateFormat } = item;
                if (key === childProps[bindKey]) {
                    if (value instanceof Date) {
                        value = formatDate(value, dateFormat);
                    }
                    if (decimalCount) {
                        value = +parseFloat(parseFloat(value.toString()).toFixed(decimalCount)).toPrecision(12);
                    }
                    if (unit) {
                        value = `${value} ${unit}`;
                    }
                }
            }
            return value;
        };
        this.onDetailPageChange = (status) => {
            let currentOrder = this.state.currentOrder;
            switch (status) {
                case 'last':
                    currentOrder--;
                    break;
                case 'next':
                    currentOrder++;
                    break;
                default:
                    break;
            }
            this.setState({ currentGroup: 'detail-page', currentOrder });
        };
        this.renderDetailPage = () => {
            const { config, formatControls, primaryKey, dataSource } = this.props;
            let result = [];
            dataSource.map((item, i) => {
                const dataItem = formatControls(item, config, primaryKey);
                result.push(<TransformManagerItem group="detail-page" order={i} key={`detail-page-${i}`}>
          <DetailFactory onBack={this.backToList} currentOrder={this.state.currentOrder} minPage={0} maxPage={dataSource.length - 1} onPageChange={this.onDetailPageChange} dataSource={dataItem} onDataFormat={this.handleChildDataFormat} onMapBoxChange={this.handleMapBoxChange}/>
        </TransformManagerItem>);
            });
            return result;
        };
        this.handleMapBoxChange = ({ lat, lng, key }) => {
            const { currentGroup, currentOrder } = this.state;
            // record position, for going back
            this.history = {
                group: currentGroup,
                order: currentOrder,
            };
            this.setState({
                currentGroup: 'map-box',
                currentOrder: 0,
                lat,
                lng,
                mapBoxTargetKey: key || '',
            });
        };
        this.handleBackFromMapBox = () => {
            const { group, order } = this.history;
            const { primaryValue, mapBoxTargetKey, lat, lng, updatePageStatus, } = this.state;
            const { onMapPickerChange } = this.props;
            onMapPickerChange &&
                onMapPickerChange({
                    lat: lat.toString(),
                    lng: lng.toString(),
                    primaryValue: updatePageStatus === 'add' ? null : primaryValue,
                    targetKey: mapBoxTargetKey,
                });
            this.setState({
                currentGroup: group,
                currentOrder: order,
            }, () => {
                this.history = {
                    group: 'list-page',
                    order: -1,
                };
            });
        };
        this.renderTemplate = () => {
            const { children, dataSource, loading, onSearch } = this.props;
            return (<TransformManagerItem group="list-page" order={0} key="list-page-0">
        {this.renderSearchBar(<Swiper wrapperHeight={this.state.templateHeight} loading={!!loading} onLoad={() => onSearch && onSearch()}>
            <div className="sc-content" ref={(ref) => (this.content = ref)}>
              <Template template={children} dataSource={dataSource} onDataFormat={this.handleChildDataFormat} onClick={this.handleTemplateClick} onPress={this.handleTemplatePress}/>
            </div>
          </Swiper>)}
      </TransformManagerItem>);
        };
        this.renderUpdatePage = () => {
            const { config, dataSource, primaryKey, formatControls, updatePageMapBoxItem, updatePageChange, updatePageForm, updatePageSave, } = this.props;
            const { primaryValue, updatePageStatus } = this.state;
            const dataItemIndex = dataSource.findIndex(item => item[primaryKey] === primaryValue);
            const dataItem = updatePageStatus === 'add'
                ? formatControls(null, config, primaryKey)
                : formatControls(dataSource[dataItemIndex], config, primaryKey);
            return (<TransformManagerItem group="update-page" order={0} key="update-page-0">
        <UpdatePage onBack={this.backToList} dataSource={dataItem} status={updatePageStatus} onMapBoxChange={this.handleMapBoxChange} updatePageMapBoxItem={updatePageMapBoxItem} updatePageForm={updatePageForm} updatePageSave={updatePageSave} onFormChange={updatePageChange}/>
      </TransformManagerItem>);
        };
        this.renderMapBox = () => {
            const { lat, lng } = this.state;
            return (<TransformManagerItem group="map-box" order={0} key="map-box-0">
        <MapBox center={{ lat, lng }} onMarkerDrag={({ lat, lng }) => {
                this.setState({ lat: lat.toString(), lng: lng.toString() });
            }}/>
        <List renderHeader="坐标信息" style={{
                position: 'absolute',
                bottom: 0,
                width: '100%',
                zIndex: 400,
            }}>
          <List.Item extra={lng}>经度</List.Item>
          <List.Item extra={lat}>纬度</List.Item>
          <List.Item>
            <Button onClick={this.handleBackFromMapBox}>返回</Button>
          </List.Item>
        </List>
      </TransformManagerItem>);
        };
        this.renderFunctionalButton = () => {
            const { dataSource, onSort, config } = this.props;
            let sortBy = [];
            for (let item of config) {
                const { isSort, key, name } = item;
                if (isSort) {
                    sortBy.push({
                        key,
                        text: name,
                    });
                }
            }
            return (<FunctionalButton sortBy={sortBy} dataSource={dataSource} onSort={onSort}/>);
        };
        this.renderSearchBar = (children) => {
            const props = this.props;
            const { showSearchBar } = this.state;
            return (<SearchBar dataSource={props.formatControls(null, props.config, props.primaryKey)} visible={showSearchBar} onVisibleChange={showSearchBar => this.setState({ showSearchBar })} children={children}/>);
        };
        this.render = () => {
            const { state, props } = this;
            const { currentOrder, currentGroup } = state;
            return (<div className="Container-core">
        <TransformManager currentGroup={currentGroup} currentOrder={currentOrder}>
          {this.renderTemplate()}
          {this.renderDetailPage()}
          {this.renderUpdatePage()}
          {this.renderMapBox()}
        </TransformManager>
        {currentGroup === 'list-page' && this.renderFunctionalButton()}
        <ActivityIndicator animating={props.loading} text="正在加载..." toast size="large"/>
      </div>);
        };
    }
    componentDidUpdate(_, prevState) {
        if (this.content &&
            this.content.clientHeight !== prevState.templateHeight) {
            this.setState({ templateHeight: this.content.clientHeight });
        }
    }
}
