import React from 'react';
import './css/Swiper.css';
import classNames from 'classnames';
const down = './assets/down.png';
const loading = './assets/loading.gif';
const complete = './assets/complete.png';
export default class Swiper extends React.Component {
    constructor() {
        super(...arguments);
        this.startY = 0;
        this.endY = 0;
        this.scrollerHeight = 10000;
        this.bottomHeight = 0;
        this.state = {
            distance: 0,
            iconDeg: 0,
            refreshEnd: false,
            loadEnd: false,
            refreshText: '下拉刷新',
            refreshImg: down,
            loadText: '加载更多',
            loadImg: down,
            status: 'loaded',
            type: 'load',
        };
        this.componentDidMount = () => {
            this.bindEvent(this.wrapper);
        };
        this.getChildHeight = () => {
            const { wrapperHeight } = this.props;
            const { scrollHeight } = this.scroller;
            this.bottomHeight = scrollHeight - wrapperHeight + 44;
            this.scrollerHeight = scrollHeight;
        };
        this.bindEvent = (refs) => {
            const { sensibility = 1 } = this.props;
            refs.addEventListener('touchstart', (e) => {
                // record coordinate before touch start
                this.startY = e.touches[0].pageY;
                this.setState({ refreshEnd: false, loadEnd: false });
            });
            refs.addEventListener('touchmove', (e) => {
                e.preventDefault();
                // distance of wrapper moving
                const distance = (e.touches[0].pageY - this.startY) / sensibility + this.endY;
                let iconDeg;
                if (distance > 44) {
                    // 44是刷新、加载div的高度，超过时箭头向上
                    iconDeg = 180;
                }
                else if (distance >= 0 && distance <= 44) {
                    // 刷新没超过上方div高度时，根据比例变换箭头方向
                    iconDeg = (distance / 44) * 180;
                }
                else {
                    // 下方的加载偷懒没写箭头变换以及未超过44的行为，mark
                    iconDeg = 180;
                }
                this.setState({ distance, iconDeg });
            });
            refs.addEventListener('touchend', () => {
                const { onRefresh, onLoad } = this.props;
                const { distance } = this.state;
                if (distance > 44) {
                    // 拖到顶部的情况
                    if (onRefresh) {
                        this.setState({
                            refreshImg: loading,
                            refreshText: '刷新中...',
                            distance: 44,
                            status: 'loading',
                            type: 'refresh',
                        });
                        onRefresh();
                    }
                }
                else if (distance > 0 && distance <= 44) {
                    // 上方div未超过44的情况
                    this.setState({ distance: 0 });
                }
                else if (distance < 0) {
                    // 拖动到底部的情况
                    const { offsetHeight, scrollHeight } = this.scroller;
                    const differHeight = scrollHeight - offsetHeight;
                    // 超出边界回弹
                    // 这里不能写等于，不然容器拖到底部时，touchstart会触发加载事件
                    if (Math.abs(distance) > differHeight) {
                        this.endY = -differHeight;
                        // 下拉加载
                        if (onLoad) {
                            this.setState({
                                loadText: '加载中...',
                                loadImg: loading,
                                distance: -this.bottomHeight,
                                status: 'loading',
                                type: 'load',
                            });
                            onLoad();
                        }
                        else {
                            this.setState({ distance: -differHeight });
                        }
                    }
                    else {
                        // 未超出边界就停在那儿
                        this.endY = distance;
                    }
                }
                this.setState({
                    refreshEnd: true,
                    loadEnd: true,
                });
            });
        };
        this.generateStyle = () => {
            const { wrapperHeight, duration = 1 } = this.props;
            const { distance, refreshEnd, loadEnd } = this.state;
            const clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
            const refreshStyle = {};
            const loadStyle = {};
            let wrapperStyle = {};
            const transform = `translate3d(0px, ${distance}px, 0)`;
            const transition = `all ${duration}s ease`;
            refreshStyle.transform = transform;
            if (refreshEnd)
                refreshStyle.transition = transition;
            loadStyle.transform = transform;
            // loadStyle.top = this.scrollerHeight;
            if (loadEnd)
                loadStyle.transition = transition;
            wrapperStyle = Object.assign({}, refreshStyle, { height: wrapperHeight || clientHeight });
            return { wrapperStyle, loadStyle, refreshStyle };
        };
        this.renderSpinner = ({ type, isFinished, relatedStyle, iconUrl, iconDeg, relatedText, }) => {
            return (<div className={classNames({
                [type]: true,
                [`${type}-end`]: isFinished,
            })} style={relatedStyle}>
        <span className={`${type}-icon`} style={{ transform: `rotateZ(${iconDeg}deg)` }}>
          <img src={iconUrl}/>
        </span>
        <span className={`${type}-text`}>{relatedText}</span>
      </div>);
        };
    }
    componentDidUpdate(prevProps, prevState) {
        if (prevProps.loading &&
            !this.props.loading &&
            prevState.status === 'loading') {
            if (this.state.type === 'refresh') {
                this.setState({
                    status: 'loaded',
                    refreshImg: complete,
                    refreshText: '刷新成功',
                    iconDeg: 0,
                }, () => {
                    setTimeout(() => {
                        this.getChildHeight();
                        this.setState({
                            refreshImg: down,
                            distance: 0,
                            refreshText: '下拉刷新',
                        });
                    }, 1000);
                });
            }
            else if (this.state.type === 'load') {
                this.setState({
                    status: 'loaded',
                    loadImg: complete,
                    loadText: '加载完成',
                    iconDeg: 0,
                }, () => {
                    setTimeout(() => {
                        this.getChildHeight();
                        this.setState({
                            loadImg: down,
                            loadText: '加载更多',
                            distance: this.endY,
                        });
                    }, 1000);
                });
            }
        }
    }
    render() {
        const { onRefresh, onLoad, children } = this.props;
        const { iconDeg, refreshEnd, refreshText, refreshImg, loadEnd, loadText, loadImg, } = this.state;
        const { refreshStyle, wrapperStyle, loadStyle } = this.generateStyle();
        return (<div className="Swiper">
        <div ref={ref => (this.wrapper = ref)} className="wrapper">
          <div style={wrapperStyle} ref={ref => ref && (this.scroller = ref)}>
            {children}
          </div>
          {onRefresh &&
            this.renderSpinner({
                type: 'refresh',
                isFinished: refreshEnd,
                relatedStyle: refreshStyle,
                iconUrl: refreshImg,
                iconDeg,
                relatedText: refreshText,
            })}
          {onLoad &&
            this.renderSpinner({
                type: 'load',
                isFinished: loadEnd,
                relatedStyle: loadStyle,
                iconUrl: loadImg,
                iconDeg,
                relatedText: loadText,
            })}
        </div>
      </div>);
    }
}
