import React, { Component } from 'react';
import { List, Button, InputItem, DatePicker, Picker, Accordion, Checkbox, Toast, Calendar, Drawer, } from 'antd-mobile';
const { CheckboxItem } = Checkbox;
import { formatDate } from '../../utils';
const CalendarDefaultValue = {
    calendarVisible: false,
    currentCalendarItem: {
        key: '',
        config: {},
    },
};
export default class SearchBar extends Component {
    constructor() {
        super(...arguments);
        this.state = Object.assign({ form: [] }, CalendarDefaultValue);
        this.checkValue = (value, item) => {
            const { type } = item;
            let rest = {};
            const index = this.state.form.findIndex(f => f.key === item.key);
            switch (type) {
                case 'checkbox':
                    let stateValue;
                    stateValue = this.state.form[index].value || '';
                    let checkboxSet = new Set();
                    if (stateValue) {
                        checkboxSet = new Set(stateValue.split(','));
                        checkboxSet.has(value)
                            ? checkboxSet.delete(value)
                            : checkboxSet.add(value);
                    }
                    else {
                        checkboxSet.add(value);
                    }
                    value = Array.from(checkboxSet).toString();
                    break;
                case 'calendar':
                    const { startDateTime, endDateTime } = value;
                    value = [formatDate(startDateTime), formatDate(endDateTime)];
                    rest = CalendarDefaultValue;
                    break;
                case 'upload':
                    const fileList = this.state.form[index].value || [];
                    fileList.push({
                        url: value.url,
                        id: value.id,
                        name: value.name,
                    });
                    value = fileList;
                    break;
                default:
                    break;
            }
            this.state.form[index] = Object.assign(this.state.form[index], {
                value,
            });
            this.setState(Object.assign({ form: this.state.form }, rest));
        };
        this.renderSidebar = () => {
            const { state } = this;
            const prefixCls = `search-bar`;
            let element = [];
            for (let item of state.form) {
                const { type, name, key, foreignData, id } = item;
                switch (type) {
                    case 'input':
                        element.push(<List.Item key={`${prefixCls}-input-item-${id}`} extra={<InputItem clear placeholder="请输入" style={{ textAlign: 'right' }} onErrorClick={() => Toast.fail(item.message)} onChange={(value) => this.checkValue(value, item)} value={item.value} error={item.error}/>}>
              {name}
            </List.Item>);
                        break;
                    case 'datePicker':
                        element.push(<DatePicker key={`${prefixCls}-data-picker-${id}`} onChange={(value) => this.checkValue(value, item)} value={item.value}>
              <List.Item arrow="horizontal">{name}</List.Item>
            </DatePicker>);
                        break;
                    case 'select':
                        element.push(<Picker key={`${prefixCls}-select-${id}`} data={foreignData} cols={1} onChange={(value) => this.checkValue(value[0], item)} value={item.value ? [item.value] : []}>
              <List.Item arrow="horizontal">{name}</List.Item>
            </Picker>);
                        break;
                    case 'checkbox':
                        element.push(<Accordion key={`${prefixCls}-checkbox-${id}`}>
              <Accordion.Panel header={name}>
                <List>
                  {foreignData.map((checkboxItem, i) => {
                            const stateValue = (item.value && item.value.split(',')) || [];
                            return (<CheckboxItem key={`${prefixCls}-checkbox-item-${i}`} checked={stateValue.includes(checkboxItem.value)} onChange={() => this.checkValue(checkboxItem.value, item)}>
                          {checkboxItem.label}
                        </CheckboxItem>);
                        })}
                </List>
              </Accordion.Panel>
            </Accordion>);
                        break;
                    case 'calendar':
                        element.push(<React.Fragment key={`${prefixCls}-calendar-${id}`}>
              <List.Item extra={item.value[0] ? '' : '请选择'} arrow="horizontal" onClick={() => this.setState({
                            calendarVisible: true,
                            currentCalendarItem: { type, key, config: item },
                        })}>
                {name}
              </List.Item>
              <List.Item extra={item.value[0]}>起始时间</List.Item>
              <List.Item extra={item.value[1]}>结束时间</List.Item>
            </React.Fragment>);
                        break;
                    default:
                        break;
                }
            }
            return element;
        };
    }
    static getDerivedStateFromProps(prevProps, prevState) {
        const newState = {};
        if (prevProps.dataSource.length !== 0 && prevState.form.length === 0) {
            newState.form = prevProps.dataSource;
        }
        return newState;
    }
    render() {
        const { visible, onVisibleChange, className, children } = this.props;
        const { calendarVisible, currentCalendarItem } = this.state;
        const sidebar = (<List renderHeader={<span style={{ fontSize: 17 }}>搜索面板</span>}>
        <List.Item>{this.renderSidebar()}</List.Item>
        <List.Item>
          <Button type="primary">确定</Button>
        </List.Item>
      </List>);
        return (<React.Fragment>
        <Drawer sidebar={sidebar} position="left" open={visible} className={className} onOpenChange={visible => onVisibleChange && onVisibleChange(visible)} sidebarStyle={{ width: '77%', background: '#fff' }} overlayStyle={{ backgroundColor: 'transpent' }} style={{
            minHeight: document.documentElement.clientHeight ||
                document.body.clientHeight,
        }}>
          {children || <React.Fragment />}
        </Drawer>
        <Calendar visible={calendarVisible} onCancel={() => {
            this.setState({ calendarVisible: false });
        }} pickTime onConfirm={(startDateTime, endDateTime) => this.checkValue({ startDateTime, endDateTime }, currentCalendarItem.config)}/>
      </React.Fragment>);
    }
}
