import React, { Component } from 'react';
import { List, Button } from 'antd-mobile';
import { bindTouchDirection } from '../../utils';
import Arrow from './DetailArrow';
export default class DetailFactory extends Component {
    constructor() {
        super(...arguments);
        this.componentDidMount = () => {
            bindTouchDirection(this.content, direction => {
                const { onPageChange, currentOrder, minPage, maxPage } = this.props;
                switch (direction) {
                    case 'toRight':
                        if (currentOrder !== minPage) {
                            onPageChange && onPageChange('last');
                        }
                        break;
                    case 'toLeft':
                        if (currentOrder !== maxPage) {
                            onPageChange && onPageChange('next');
                        }
                        break;
                    default:
                        break;
                }
            });
        };
        this.handleControls = (item, index) => {
            const { onDataFormat, onMapBoxChange } = this.props;
            const { type, value, name } = item;
            if (type === 'mapPicker') {
                return (<React.Fragment key={`detail-page-map-picker-${index}`}>
          <List.Item extra={item.lng}>经度</List.Item>
          <List.Item extra={item.lat}>纬度</List.Item>
          <List.Item extra={item.address} arrow="horizontal" onClick={() => onMapBoxChange && onMapBoxChange(item)}>
            地址
          </List.Item>
        </React.Fragment>);
            }
            if (type === 'calendar') {
                return (<React.Fragment key={`detail-page-label-${index}`}>
          <List.Item extra={onDataFormat
                    ? onDataFormat(value[0], item, 'key')
                    : value}>
            {name + '开始时间'}
          </List.Item>
          <List.Item extra={onDataFormat
                    ? onDataFormat(value[1], item, 'key')
                    : value}>
            {name + '结束时间'}
          </List.Item>
        </React.Fragment>);
            }
            if (type !== 'mapPicker' && type !== 'upload') {
                return (<List.Item key={`detail-page-label-${index}`} extra={onDataFormat ? onDataFormat(value, item, 'key') : value}>
          {name}
        </List.Item>);
            }
            return null;
        };
        this.render = () => {
            const { onBack, dataSource = [], onPageChange, currentOrder, minPage, maxPage, showArrow, } = this.props;
            return (<div className="DetailFactory" ref={ref => ref && (this.content = ref)}>
        {showArrow && (<Arrow onClick={onPageChange} showLast={currentOrder !== minPage} showNext={currentOrder !== maxPage}/>)}

        <List>
          <List.Item>
            {dataSource.map((item, i) => this.handleControls(item, i))}
          </List.Item>
          <List.Item>
            <Button onClick={onBack}>返回</Button>
          </List.Item>
        </List>
      </div>);
        };
    }
}
