import React, { Component } from 'react';
import Container from './core';
import { ajax } from '../../utils/urlHelper';
import { Toast } from 'antd-mobile';
import { formatConfig, formatControls } from './utils';
export default class DataController extends Component {
    constructor() {
        super(...arguments);
        this.state = {
            config: [],
            dataSource: [],
            total: 0,
            power: {
                select: false,
                delete: false,
                update: false,
                add: false,
            },
            primaryKey: '',
            loading: false,
            updatePageMapBoxItem: { lng: '-1', lat: '-1', key: 'key' },
            updatePageForm: [],
        };
        this.componentDidMount = () => {
            this.getConfig();
        };
        this.handlePowerStr = (power) => {
            let result = {
                select: false,
                delete: false,
                update: false,
                add: false,
            };
            for (let item of Array.from(power.split(','))) {
                switch (item) {
                    case 'Select':
                        result.select = true;
                        break;
                    case 'Add':
                        result.add = true;
                        break;
                    case 'Del':
                        result.delete = true;
                        break;
                    case 'Update':
                        result.update = true;
                        break;
                    default:
                        break;
                }
            }
            return result;
        };
        this.getConfig = () => {
            if (!this.state.loading) {
                this.setState({ loading: true });
            }
            const { tableId, menuId } = this.props;
            ajax({
                url: './assets/getConfig.json',
                data: { tableId, menuId },
                success: ({ data }) => {
                    const primaryKey = this.getPrimaryKey(data.tablefieldconfig);
                    this.setState({
                        config: formatConfig(data.tablefieldconfig),
                        power: this.handlePowerStr(data.power),
                        primaryKey,
                    });
                    this.search();
                },
            });
        };
        this.getPrimaryKey = (data) => {
            const keyItem = data.filter((item) => item.iskey);
            if (!keyItem.length) {
                console.error('where is the primary key? please check.');
                return;
            }
            return keyItem[0]['fname'];
        };
        this.search = () => {
            if (!this.state.loading) {
                this.setState({ loading: true });
            }
            ajax({
                url: './assets/search.json',
                success: ({ data }) => {
                    let dataSource = [];
                    data.list.map((item, i) => {
                        dataSource.push(Object.assign({}, item, { templateOrder: i }));
                    });
                    this.setState({
                        dataSource,
                        total: data.recordcount,
                        loading: false,
                    });
                },
            });
        };
        this.handleDelete = (primaryValue) => {
            if (!this.state.loading) {
                this.setState({ loading: true });
            }
            ajax({
                url: './assets/operatedata.json',
                data: { id: primaryValue },
                success: ({ data }) => {
                    if (data.result) {
                        this.search();
                    }
                    else {
                        this.setState({ loading: false });
                        Toast.fail('保存失败：' + data.remark || '');
                    }
                },
            });
        };
        this.handeMapPickerChange = ({ primaryValue, targetKey, lng, lat, }) => {
            const { dataSource, primaryKey } = this.state;
            if (!primaryValue) {
                this.setState({ updatePageMapBoxItem: { lat, lng, key: targetKey } });
            }
            else {
                const itemIndex = dataSource.findIndex(item => item[primaryKey] === primaryValue);
                dataSource[itemIndex] = Object.assign({}, dataSource[itemIndex], {
                    [targetKey]: `${lng}|${lat}|`,
                });
                this.setState({ dataSource });
            }
        };
        this.getUploadParam = (file) => {
            const param = new FormData();
            param.append('Filedata', file);
            param.append('Filename', file.name);
            param.append('fileext', '*.' + file.name.split('.')[file.name.split('.').length - 1]);
            param.append('DataType', 'UploadFile');
            param.append('UploadFolder', '/Attachement/');
            param.append('IsConvertOffice', '');
            param.append('GetFileName', 'y');
            param.append('TCID', '');
            param.append('UploadTargetKey', 'n');
            param.append('GetFileInfo', 'y');
            return param;
        };
        this.handleSort = (dataSource) => {
            this.setState({ dataSource });
        };
        // todo: refactor this
        // combine dataSource and updatePageForm
        this.handleUpdatePageChange = (updatePageForm) => {
            const { updatePageMapBoxItem: mapItem } = this.state;
            const index = updatePageForm.findIndex(f => f.key === mapItem.key);
            if (index !== -1) {
                updatePageForm[index] = Object.assign({}, updatePageForm[index], mapItem);
                updatePageForm[index].value = `${mapItem.lng}|${mapItem.lat}|`;
            }
            this.setState({ updatePageForm });
        };
        this.handleUpdatePageSave = (updatePageForm) => {
            console.log(updatePageForm);
        };
        this.render = () => {
            const { power, config, dataSource, loading, total, primaryKey, updatePageMapBoxItem, updatePageForm, } = this.state;
            return (<div className="DataController">
        <Container {...this.props} power={power} config={config} dataSource={dataSource} total={total} primaryKey={primaryKey} loading={loading} onSearch={this.search} onDelete={this.handleDelete} formatControls={formatControls} onMapPickerChange={this.handeMapPickerChange} updatePageMapBoxItem={updatePageMapBoxItem} updatePageForm={updatePageForm} updatePageChange={this.handleUpdatePageChange} updatePageSave={this.handleUpdatePageSave} onSort={this.handleSort}/>
      </div>);
        };
    }
}
DataController.defaultProps = {
    children: null,
    tableId: -1,
    menuId: -1,
};
